.Blink-text-container {
    width: 100%;
    text-align: left;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-bottom: 0.4em;
}


.Blink-text-input {
    border: 0px;
    text-align: left;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0.5em 1em;
    font-family: inherit;
    font-size: inherit;
    outline: none;
    
}


.Bink-text-separator {
    width: 100%;
    border-bottom: 1px solid;
    opacity: 0;
    -webkit-transition: opacity 0.5s ease;
    -o-transition: opacity 0.5s ease;
    transition: opacity 0.5s ease;
}


.Blink-text-caption {
    font-size: 0.4em;
    opacity: 0;
    -webkit-transition: opacity 0.5s ease;
    -o-transition: opacity 0.5s ease;
    transition: opacity 0.5s ease;
}

.Blink-text-caption.focus {
    opacity: 1;
}


.Blink-text-input:focus + .Bink-text-separator {
    opacity: 1;
}




