.Blink-button-container {
    cursor: pointer;
    padding-left: 1em;
    padding-right: 1em;
    padding-top: 0.2em;
    padding-bottom: 0.2em;
    border: 1px solid;
    -webkit-transition: all 500ms ease;
    -o-transition: all 500ms ease;
    transition: all 500ms ease;
}

.Blink-button-container:hover {
    background-color: #f55d5d;
    color: white;
}

.Blink-button-disabled {
    pointer-events: none;
    opacity: 0.5;
}


